import Vue from 'vue'
import Router from 'vue-router'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/index'
    },
    {
      path: '/index',
      name: 'index',
      component: resolve => require(['@/pages/index.vue'], resolve)
    },
    {
      path: '/news',
      name: 'news',
      component: resolve => require(['@/pages/news.vue'], resolve)
    },
    {
      path: '/newsDetail',
      name: 'newsDetail',
      component: resolve => require(['@/pages/newDetail.vue'], resolve)
    },
    {
      path: '/resource',
      name: 'resource',
      component: resolve => require(['@/pages/resource.vue'], resolve)
    },
    {
      path: '/resourceDetail',
      name: 'resourceDetail',
      component: resolve => require(['@/pages/resourceDetail.vue'], resolve)
    }
  ]
})



